      min: new Date(2001, 0, 1), //further back you can go
      // max: new Date(2016, 0, 1),
      zoomMin: 1000 * 60 * 60 * 24            // one day in milliseconds, furthest "in"
      // zoomMax: 1000 * 60 * 60 * 24 * 31 * 3   // about three months in milliseconds
